module.exports = "/**\n * Applies styles for users in high contrast mode. Note that this only applies\n * to Microsoft browsers. Chrome can be included by checking for the `html[hc]`\n * attribute, however Chrome handles high contrast differently.\n * @param target Which kind of high contrast setting to target. Defaults to `active`, can be\n *    `white-on-black` or `black-on-white`.\n */\n/* Theme for the ripple elements.*/\n/* stylelint-disable material/no-prefixes */\n/* stylelint-enable */\n:host {\n  height: 100%;\n  left: 0;\n  position: fixed;\n  z-index: 10; }\n:host,\n  :host * {\n    overflow: visible; }\n:host .menu {\n    background-color: #3f51b5;\n    font-size: 1.5em;\n    font-weight: bold;\n    height: 100%;\n    list-style: none;\n    min-width: 7em;\n    position: absolute;\n    padding-top: 10px;\n    right: 0;\n    top: 0;\n    transition: .25s;\n    width: 6em; }\n:host .menu .active-link-container {\n      background-color: #e91e63 !important;\n      border-bottom-left-radius: 15%;\n      border-top-left-radius: 15%;\n      -webkit-transform: translate(5%);\n              transform: translate(5%);\n      transition: 200ms ease-in;\n      visibility: hidden;\n      box-shadow: 0px 0px 0px 0px #333; }\n:host .menu-show {\n      box-shadow: 0px 0px 0px 0px #333;\n      -webkit-transform: translateX(100%);\n              transform: translateX(100%); }\n:host .menu-show .active-link-container {\n        visibility: visible;\n        transition: 200ms; }\n:host .menu .link-container {\n      margin-top: 0;\n      padding-top: 6px;\n      padding-bottom: 6px;\n      background-color: #3f51b5; }\n:host .menu .link-container a {\n        padding: 0;\n        text-decoration: none;\n        color: white;\n        font-size: 1.18em;\n        position: relative; }\n:host .menu .link-container a:hover {\n          background-color: transparent;\n          color: gold; }\n:host .menu .link-container a:hover::after {\n            visibility: visible;\n            width: 100%; }\n:host .menu .link-container a::after {\n          border: 2px solid gold;\n          content: '';\n          display: block;\n          position: absolute;\n          transition: 200ms ease-out;\n          visibility: hidden;\n          width: 0;\n          left: 0; }\n\n/*# sourceMappingURL=data:application/json;base64, */"